body {
  margin: 0;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.4;
  -webkit-font-smoothing: antialiased;
  color: #484848;
  background-color: white; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  text-decoration: none; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.site-wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column; }

.container {
  padding: 0 17px;
  max-width: 1166px;
  min-width: 320px;
  width: 100%;
  margin: 0 auto; }

.intro {
  min-height: 908px;
  background-color: #e5e7ea;
  background-image: url("../img/desktop/main-header__bg@1x.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .intro {
    background-image: url("../img/desktop/main-header__bg@2x.jpg"); } }

.main-header {
  display: flex;
  justify-content: space-between; }
